.App {
  text-align: center;
  position: relative;
  
}

.addedText{
  font-size: 60px;
  color: #293cdf;
}
.fade-enter{
  opacity: 0
}
.fade-enter-active{
  opacity: 1;
  transition: opacity 3s;
}
.fade-exit{
  opacity: 1;
}
.fade-exit-active{
  opacity: 0;
  transition: opacity 0.5s;
}
.cursor{
    height: 5px;
    margin-bottom: -4px;
    width: 55px;
    background: #000000;
    opacity: 0;
    display: inline-block;
    animation: blink 0.7s linear infinite alternate;
    position: absolute;
    bottom: -55px;
    left: 50%;
    transform: translate(-50%,0)
}
@keyframes blink {
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.fullText{
  position: absolute;
  width: 100%;
  bottom: -100px;
  text-align: center;
}
.code{
  position: fixed;
  top: 150px;
  left: 50px;
  font-size: 25px;
  color: #293cdf;
}
